<!-- eslint-disable -->
<template>
  <div class="apresentacao-wrapper movement">
    <div id="apresentacao">
      <div class="copy" v-html="text"></div>
      <div class="line"></div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import img from "../../assets/cadeira.png";
import axios from "axios";

export default {
  data() {
    return {
      img: img,
      text: "",
    };
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://www.anacarolinapereira.pt/db-api/getServicos.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var intro = JSON.parse(result[1]);

        vm.text = intro[0].text;
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.apresentacao-wrapper {
  background-color: $white;
  height: pxToEm(250);
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) and (max-width: 1919px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    padding: 0 pxToEm(20);
  }

  #apresentacao {
    height: 100%;
    margin: $mg-desktop;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .copy {
      color: #8e8e8e;
      font-size: pxToEm(16);
      max-width: pxToEm(935);
      width: 100%;
      text-align: center;
    }

    .line {
      width: pxToEm(80);
      height: pxToEm(1);
      background-color: $turquiose;
      margin-top: pxToEm(30);
    }
  }
}
</style>



